import React, {Component} from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'

import index_config from '@modules/statistical/manage/index_config'

class App extends Component {

  render() {
    return(
      <div className="rejudge main adc-manage-main">
        <div className="manage-area">
          <div className="top-title-box">
            <div>
              <h1>我是主页</h1>

                <Switch>
                  <Route path="/statistical_manage/index" component={index_config}/>
                </Switch>

            </div>
          </div>
        </div>
      </div>

    )
  }

}

export default App
